/*--------------------------------------------------------------
# Inner Circle Event Cards

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
#events .col {
  margin-bottom: 1rem; }

#events .card-ic-event .card-header {
  padding: 1rem 1.5rem; }

#events .card-ic-event .card-details {
  padding: 1rem 1.5rem; }
  #events .card-ic-event .card-details svg {
    margin-right: 0.5rem; }

/*--------------------------------------------------------------
2. Desktop
--------------------------------------------------------------*/
@media (min-width: calc(992px)) {
  #events .col {
    margin-bottom: 2rem; }
  #events .card-ic-event {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 0px 0px; }
    #events .card-ic-event .card-header {
      grid-column: 1 / span 1;
      padding: 1rem 1.5rem 2rem 1.5rem; }
    #events .card-ic-event .card-details {
      grid-column: 2 / span 1;
      padding-left: 0;
      padding-top: 2rem; } }

/*--------------------------------------------------------------
# Inner Circle - Single Post

1. Mobile
2. Desktop
--------------------------------------------------------------*/
/* 
Media queries modeled on those found in Bootstrap.
See: https://getbootstrap.com/docs/4.5/layout/overview/#responsive-breakpoints for context.
*/
/*--------------------------------------------------------------
1. File Attachment (.downloads)
--------------------------------------------------------------*/
.downloads {
  position: relative;
  margin: 3rem 0;
  padding-top: 3rem; }
  .downloads:before {
    content: '';
    height: 1px;
    width: 100%;
    max-width: 16rem;
    display: block;
    position: absolute;
    top: 0;
    background-color: #d0d0d0; }
  .downloads img {
    margin-right: 1.5rem; }
  .downloads img.attachment-icon {
    padding: 1.5rem;
    border: 1px solid #e8e8e8; }
  .downloads .media-body p:first-child {
    font-weight: 700; }

/*--------------------------------------------------------------
2. IC Story Hero, Mobile
--------------------------------------------------------------*/
.uds-story-hero .content .breadcrumb,
.uds-story-hero .content p.meta {
  padding: .75rem 0;
  margin: 0.25rem 0; }

.uds-story-hero .content h1 {
  margin: 2rem 0 0.25rem; }

/*--------------------------------------------------------------
3. IC Story Hero, Desktop
--------------------------------------------------------------*/
@media (min-width: 950px) {
  .content .breadcrumb,
  .content p.meta {
    margin: 1.25rem 0 2.25rem; } }

/*--------------------------------------------------------------
# Inner Circle - Tag.php

1. Mobile
2. Desktop
--------------------------------------------------------------*/
.archive.tag #tag-hero img {
  margin-top: 1rem; }

.archive.tag .entry-footer {
  display: inline-block; }
  .archive.tag .entry-footer svg {
    margin-right: 0.5rem; }
  .archive.tag .entry-footer svg.fa-clock, .archive.tag .entry-footer svg.fa-map-marker-alt {
    margin-left: 1.5rem; }
  .archive.tag .entry-footer .card-tags {
    margin-top: 1rem;
    padding: 0; }
